/* Css Fonts Imported */
@import url('https://fonts.googleapis.com/css2?family=Readex+Pro:wght@200;300;400;500;600;700;800&display=swap');

/* Css Reset  Start */
* {
  margin:0;
  padding: 0;
  font-family: 'Readex Pro', sans-serif;
}

h2{
  font-style: italic;
}


.primary-title {
  font-size: 90px;
  color: rgba(255,255,255);
}

.primary-button {
  color: #fff;
  text-transform: uppercase;
  font-size:15px;
  background: linear-gradient(45deg, rgb(254, 48, 114), rgb(255, 89, 64));
  padding: 12px 30px;
  border-radius: 30px;
  border: none;
}


.primary-button:hover {
  background: linear-gradient(260deg, rgb(254, 48, 114), rgb(255, 89, 64));
  cursor: pointer;
}

.secondary-button {
  color: rgb(121, 119, 119);
  background-color: rgb(255, 255, 255);
  font-weight: 500;
  font-size: 15px;
  text-transform: uppercase;
  padding: 12px 30px;
  border-radius: 30px;
  border: solid 2px rgb(121, 119, 119);
  margin: 6px;
}

.secondary-button:hover {
  color: rgb(48,48,48);
  border: solid 2px rgb(48,48,48);
  cursor: PO;
}


body{
  text-align: center;
}

/* Css Reset  End */


/* Css NavBar Start */
nav {
  width: 100%;
  display: flex;
  justify-content: space-between;
}

nav .nav-button {
  color: rgb(254, 48, 114);
  background-color: white;
  font-size: 20px;
  padding: 10px 20px;
  border-radius: 10px;
  border: none;
  margin: 10px;
}

nav .nav-button:hover {
  color: rgb(217, 41, 97);
  cursor: pointer;
}


nav .nav-button:disabled {
  background-color: rgb(181,180,180);
  color: rgb(160,38,75);
}


nav .logo-container {
  width: 170px;
  margin: 15px;
}

nav .logo-container .logo{
  width: 100%;
}

/* Css NavBar End */


/* Css NavBar Start */

/* Css Home Start */
.overlay {
  width: 100vw;
  height: 100vh;
  position: fixed;
  background: linear-gradient(
    to bottom,
    rgba(0, 0, 0, 0.65) 0%,
    rgba(0, 0, 0, 0) 100%
), url('images/6e9794bcedeecf5a8f8f41338a2a7345.webp')
}


.home  {
  margin-top: 30vh;

}
/* Css Home End */



/* Css Auth Modal Start */
.auth-modal {
  position: absolute;
  left: 0;
  right: 0;
  top: 50px;
  margin-left: auto;
  margin-right: auto;
  max-width: 360px;
  height: 600px;
  background-color: rgb(255, 255, 255);
  border-radius: 10px;
  padding: 40px;
}

.auth-modal h2{
  color: black;
  margin: 20px 0;
}

.auth-modal .close-icon {
  float: right;
}

.auth-modal .close-icon:hover {
  color:#d6002f;
  cursor: pointer;
}

.auth-modal form {
  display: flex;
  flex-direction: column;
  margin-top: 30px;
}

.auth-modal form  input[type='submit']:hover {
  cursor: pointer;
}

.auth-modal form div.MuiFormControl-root {
  margin: 7px 0;
}

.auth-modal form input[type=password],
.auth-modal form input[type=email]
 {
  padding: 8px;
  margin: 8px;
  font-size: 17px;
}
/* Css Auth Modal End */


/* Css OnBoarding  Page Start */

.onboarding  form {
  display: flex;
  justify-content:center;
}




.onboarding form section {
  display: flex;
  flex-direction: column;
  padding: 20px;
  text-align: start;
}

.onboarding form input {
  padding: 15px 30px;
  margin: 10px 0;
  font-size: 15px;
}

.onboarding form .multiple-input-container{
  display: flex;
  flex-direction: row;
}

.onboarding form .multiple-input-container input {
 margin-right: 10px;
}


.onboarding form label {
  margin: 10px 0;
}

.onboarding form .multiple-input-container label {
  padding: 10px;
  border: solid 2px rgb(144,144,144);
  border-radius: 10px;
  transition: all 0.3;
  margin-right: 10px;
 }

 .onboarding form input#show-gender {
  width: 20px;
 } 
 

 .onboarding form  input[type='radio'] {  
    display: none;
 }


 .onboarding form  input[type='number'] {  
  width:10% ;
}

 .onboarding form  input[type='radio']:checked + label {  
  border: solid 2px rgb(221,39,22);
}

.onboarding form  input[type='submit']:hover {
  background-color: rgb(235,235,235);
  cursor: pointer;
}

.onboarding form  input[type='submit']:active {
  background-color: rgb(226,115,155);
}

.onboarding form .photo-container  {
  width: 100%;
  height: 575px;
}

.onboarding form .photo-container img {
  width: 100%;
  height: 100%;
}



/* Css OnBoarding Page End */


/* Css Dashboard Page Start */
h2 {
  color: #fff;
}

.dashboard {
  display: flex;
  justify-content: space-between;
}

.dashboard .swiper-container {
  width: 70%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.swipe {
  position: absolute;
}

.dashboard .cardContainer {
  width: 400px;
  height: 650px;
}

.card {
  position: relative;
  background-color: #fff;
  width: 400px;
  height: 650px;
  border-radius: 30px;
  background-size: cover;
  background-position: center;
}

/* .cardContent {
  width: 100%;
  height: 100%;
} */


.card h3 {
  margin-top: 0;
}


.dashboard .swipe-info {
  position: absolute;
  bottom: 0;
  padding: 10px;
}
/* Css Dashboard Page End */



/* Css Chat-Container Page Start */
.chat-container {
  background-color: rgb(255, 255, 255);
  box-shadow: rgba(0, 0, 0, 0.05) 0px 6px 24px 0px,
  rgba(0, 0, 0, 0.08) 0px 0px 0px 1px;
  width: 30%;
  text-align: left;
  z-index: 1;
}

.chat-container .chat-container-header {
  background: linear-gradient(45deg, rgb(254, 48, 114), rgb(255, 89, 64));
  height: 100px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.chat-container .option {
  border: none;
  background-color: rgb(255, 255, 255);
  border-bottom: solid 4px rgb(243, 33, 33);
  font-size: 20px;
  margin: 2px;
  padding: 10px;
}

.chat-container .option:disabled {
  border-bottom: solid 4px rgb(187, 187, 187);
}

.chat-container .profile,
.chat-container .log-out-icon {
  display: flex;
  align-items: center;
  padding: 20px;
  color: rgb(255, 255, 255);
}

.chat-container .log-out-icon:hover{
  cursor: pointer;
}

.img-container {
  height: 80px;
  width: 80px;
  border-radius: 50%;
  overflow: hidden;
  margin: 10px;
}

.img-container img {
  width: 100%;
}
/* Css Chat-Container Page End */



/* Css Chat Display Page Start */
.chat-display,
.matches-display {
    padding: 20px;
    height: 55vh;
    overflow-y: auto;
}

.chat-input {
    padding: 20px;
    display: flex;
    flex-direction: column;
}
/* Css Chat Display Page End */